.wrapper {
	width: 100%;
	height: 20vh;
	background: #000;
	color: #f5f5f7;
	font-family: 'Josefin Sans', sans-serif;
}

.inner {
	padding: 30px 40px;
	display: flex;
	justify-content: space-between;
}

.wrapper img {
	background-color: #f5f5f7;
	padding: 8px;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	margin: 0 5px;
}

.wrapper img:hover {
	cursor: pointer;
	background-color: #BFBFBFFF;
}

.title {
	display: flex;
	align-items: center;
}

.logo {
	font-size: 30px;
	margin-right: 20px;
}

.logo span {
	font-size: 35px;
	color: #50f0d0;
}

.rights {
	margin-top: 60px;
}

.secondBlock div {
	margin-top: 34px;
}

.secondBlock div span {
	color: #50f0d0;
	display: inline-block;
	margin-right: 15px;
	font-size: 15px;
}

.secondBlock div:first-child {
	margin-top: 12px;
}

@media(max-width: 600px) {
	.inner {
		padding: 14px;
	}
	.logo {
		font-size: 12px;
		margin-right: 5px;
	}
	.logo span {
		font-size: 16px;
	}
	.secondBlock div span {
		font-size: 12px;
	}
	.rights {
		margin-top: 14px;
	}
	.secdiv {
		margin: 0;
		padding: 0;
	}
}